html {
    font-size: 125%;
}
h1, h3, p {
    text-transform: capitalize;
}
h2, h3 {
    text-align: center;
}
p {
    margin-bottom: 20px;
}
h1 {
    span {
        text-transform: uppercase;
    }
}
ul {
    padding: 0;
    margin: 0;
}
ul li {
    list-style-type: none;
}
.wrapper {
    max-width: 1280px;
    width: 80%;
    margin: 0 auto;
}